<template>
	<view>
		<!-- 上啦加载布局 -->
		<view class="sl">————{{sl}}————</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				sl:'上拉加载'
			};
		},
		// 监听页面上拉加载事件
		onReachBottom() {
			if(this.sl!='上拉加载') return;
			this.sl='正在加载数据中。。。';
			setTimeout(()=>{
				this.sl='我是有底线的~';
			},3000)
		}
	}
</script>

<style lang="scss">
	.sl{
		margin-bottom: 100rpx;
		height: 80rpx;
		// line-height: 80rpx;
		text-align: center;
		color: #ccc;
	}
</style>
